import React, { Component } from "react";

type Props = {
  logged: boolean;
  currentUser: string;
  firstname: string;
  lastname: string;
};

export default class ProfilePage extends Component<Props, object> {
  render() {
    const { logged, currentUser, firstname, lastname } = this.props;
    return (
      logged && (
        <div className="dark-mode">
          <h1>Profile Page for {currentUser} </h1>
          <h2>Firstname: {firstname}</h2>
          <h2>Lastname: {lastname} </h2>
          <input placeholder="First Name" />
          <input placeholder="Last Name" />
        </div>
      )
    );
  }
}
